<template>
  <div class="post">
    <div class="header">
      <h1>{{ post.title }}</h1>
      <p class="info smaller-font-size">
        <span>发布于&nbsp;{{ getTimeAgo(post.createAt) }}&nbsp;</span>
        <span>作者&nbsp;{{ post.author.name }}&nbsp;</span>
        <span>{{ post.visitCount }}&nbsp;次浏览</span>
      </p>
    </div>
    <hr>
    <div class="content" v-html="post.content"></div>
    <hr>
    <ul class="replies">
      <li 
        class="reply"
        v-for="reply in post.replies"
        :key="reply.id"
      >
        <AuthorInfo :author="reply.author"/>
        <div v-html="reply.content"></div>
        <span class="smaller-font-size">{{ getTimeAgo(reply.createAt) }}</span>
        <span class="smaller-font-size float-right">{{reply.ups}}&nbsp;赞</span>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { getTimeAgo } from "@/util";
import AuthorInfo from "./AuthorInfo.vue";

export default {
  name: "PostDetail",
  components: { AuthorInfo },
  computed: mapState(["post"]),
  methods: { getTimeAgo }
};
</script>
  
<style scoped>
.post {
  width: 100%;
}

.header {
  padding: 0.5rem;
}

.info > span::before {
  content: "• ";
  font-size: 12px;
  font-weight: bold;
}

.content {
  padding: 1rem;
}

.replies {
  display: block;
  margin: 0;
  padding: 0;
  list-style: none;
}

.reply {
  display: block;
  padding: 0.5rem;
  border-bottom: 1px solid lightgrey;
}
</style>